.App {
  height: 100%;
  width: 100%;

  position: absolute;
  
  display: flex;
  justify-content: center;
  align-items: center;
}

.Main {
  background-color: rgba(24, 24, 24, 0.95);
  width: 16vw;

  display: flex;
  position: absolute;

  border-radius: 8px;
}

.TopRow {
  height: 40%;
  width: 100%;

  display: flex;
  vertical-align: middle;
}

.BottomRow {
  height: 60%;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
}
